"use client";

import React, {
  useState,
  useEffect,
  useRef,
  useCallback,
  useMemo,
} from "react";

interface PuzzlePiece {
  id: string;
  type: "fire" | "water" | "earth" | "air" | "light" | "dark";
  row: number;
  col: number;
  isMatched?: boolean;
  comboGroup?: number;
}

interface TimelineEvent {
  id: string;
  type: "attack" | "defend" | "special";
  target: "player" | "enemy";
  damage?: number;
  timestamp: number;
  position: number;
}

interface Ability {
  id: string;
  name: string;
  icon: string;
  cooldown: number;
  currentCooldown: number;
  damage: number;
  cost: number;
}

interface GameState {
  playerHP: number;
  enemyHP: number;
  playerMP: number;
  turn: number;
  combo: number;
  score: number;
}

interface PredictionResult {
  matches: PuzzlePiece[];
  damage: number;
  mpGain: number;
  comboCount: number;
}

const GRID_SIZE = 8;
const PIECE_TYPES: PuzzlePiece["type"][] = [
  "fire",
  "water",
  "earth",
  "air",
  "light",
  "dark",
];
const PIECE_COLORS = {
  fire: "#BF6A1F",
  water: "#5B9BD5",
  earth: "#8C441B",
  air: "#F2EB80",
  light: "#FFE4B5",
  dark: "#401A04",
};

const PuzzleBattleGame: React.FC = () => {
  const [grid, setGrid] = useState<PuzzlePiece[][]>([]);
  const [selectedPiece, setSelectedPiece] = useState<PuzzlePiece | null>(null);
  const [hoveredPiece, setHoveredPiece] = useState<PuzzlePiece | null>(null);
  const [splitterPosition, setSplitterPosition] = useState(70);
  const [isDraggingSplitter, setIsDraggingSplitter] = useState(false);
  const [timelineEvents, setTimelineEvents] = useState<TimelineEvent[]>([]);
  const [abilities, setAbilities] = useState<Ability[]>([]);
  const [gameState, setGameState] = useState<GameState>({
    playerHP: 100,
    enemyHP: 100,
    playerMP: 50,
    turn: 1,
    combo: 0,
    score: 0,
  });
  const [predictedMoves, setPredictedMoves] = useState<PuzzlePiece[]>([]);
  const [predictionData, setPredictionData] = useState<PredictionResult | null>(
    null
  );

  const [replayMode, setReplayMode] = useState(false);
  const [replayHistory, setReplayHistory] = useState<any[]>([]);
  const [currentReplayIndex, setCurrentReplayIndex] = useState(0);
  const displayGameState = gameState ?? {
    playerHP: 100,
    enemyHP: 100,
    playerMP: 50,
    turn: 1,
    combo: 0,
    score: 0,
  };
  const [menuOpen, setMenuOpen] = useState(false);

  const containerRef = useRef<HTMLDivElement>(null);
  const timelineRef = useRef<SVGSVGElement>(null);
  const mouseUpHandlerRef = useRef<((e: MouseEvent) => void) | null>(null);

  useEffect(() => {
    if (isDraggingSplitter) {
      mouseUpHandlerRef.current = () => setIsDraggingSplitter(false);
      document.addEventListener("mouseup", mouseUpHandlerRef.current);
      return () => {
        if (mouseUpHandlerRef.current) {
          document.removeEventListener("mouseup", mouseUpHandlerRef.current);
        }
      };
    }
  }, [isDraggingSplitter]);

  // Initialize grid
  useEffect(() => {
    initializeGrid();
    initializeAbilities();
  }, []);

  const initializeGrid = () => {
    const newGrid: PuzzlePiece[][] = [];
    for (let row = 0; row < GRID_SIZE; row++) {
      newGrid[row] = [];
      for (let col = 0; col < GRID_SIZE; col++) {
        let pieceType;
        do {
          pieceType =
            PIECE_TYPES[Math.floor(Math.random() * PIECE_TYPES.length)];
        } while (
          (col >= 2 &&
            newGrid[row][col - 1].type === pieceType &&
            newGrid[row][col - 2].type === pieceType) ||
          (row >= 2 &&
            newGrid[row - 1][col].type === pieceType &&
            newGrid[row - 2][col].type === pieceType)
        );
        newGrid[row][col] = { id: `${row}-${col}`, type: pieceType, row, col };
      }
    }
    setGrid(newGrid);
  };

  const initializeAbilities = () => {
    setAbilities([
      {
        id: "1",
        name: "Fireball",
        icon: "üî•",
        cooldown: 3,
        currentCooldown: 0,
        damage: 15,
        cost: 10,
      },
      {
        id: "2",
        name: "Heal",
        icon: "üíö",
        cooldown: 5,
        currentCooldown: 0,
        damage: -20,
        cost: 15,
      },
      {
        id: "3",
        name: "Thunder",
        icon: "‚ö°",
        cooldown: 4,
        currentCooldown: 0,
        damage: 25,
        cost: 20,
      },
      {
        id: "4",
        name: "Shield",
        icon: "üõ°Ô∏è",
        cooldown: 6,
        currentCooldown: 0,
        damage: 0,
        cost: 5,
      },
    ]);
  };

  const saveReplayFrame = (
    customGrid?: PuzzlePiece[][],
    customGameState?: GameState
  ) => {
    setReplayHistory((prev) => [
      ...prev,
      {
        grid: JSON.parse(JSON.stringify(customGrid ?? grid)),
        gameState: { ...(customGameState ?? gameState) },
      },
    ]);
  };

  const abilitiesWidthLg = 100 - splitterPosition;
  const puzzleWidthLg = splitterPosition;

  const handlePieceClick = (piece: PuzzlePiece) => {
    if (replayMode) return;

    if (!selectedPiece) {
      setSelectedPiece(piece);
    } else {
      if (isAdjacent(selectedPiece, piece)) {
        swapPieces(selectedPiece, piece);

        setPredictedMoves([]); // clear predictions on action
      }

      setSelectedPiece(null);
    }
  };

  const isAdjacent = (piece1: PuzzlePiece, piece2: PuzzlePiece): boolean => {
    const rowDiff = Math.abs(piece1.row - piece2.row);
    const colDiff = Math.abs(piece1.col - piece2.col);
    return (rowDiff === 1 && colDiff === 0) || (rowDiff === 0 && colDiff === 1);
  };

  const swapPieces = (piece1: PuzzlePiece, piece2: PuzzlePiece) => {
    const newGrid = [...grid];
    const temp = newGrid[piece1.row][piece1.col];
    newGrid[piece1.row][piece1.col] = newGrid[piece2.row][piece2.col];
    newGrid[piece2.row][piece2.col] = temp;

    // Update positions
    newGrid[piece1.row][piece1.col].row = piece1.row;
    newGrid[piece1.row][piece1.col].col = piece1.col;
    newGrid[piece2.row][piece2.col].row = piece2.row;
    newGrid[piece2.row][piece2.col].col = piece2.col;

    setGrid(newGrid);
    saveReplayFrame();

    // Check for matches after swap
    setTimeout(() => {
      checkMatches();
    }, 300);

    // Add to replay history
    if (!replayMode) {
      setReplayHistory([
        ...replayHistory,
        { type: "swap", piece1, piece2, grid: newGrid },
      ]);
    }
  };

  const checkMatches = () => {
    const newGrid = [...grid];
    const matches: PuzzlePiece[] = [];
    let comboGroup = 0;

    // Check horizontal matches
    for (let row = 0; row < GRID_SIZE; row++) {
      for (let col = 0; col < GRID_SIZE - 2; col++) {
        if (
          newGrid[row][col].type === newGrid[row][col + 1].type &&
          newGrid[row][col].type === newGrid[row][col + 2].type
        ) {
          comboGroup++;
          for (
            let i = col;
            i < GRID_SIZE && newGrid[row][i].type === newGrid[row][col].type;
            i++
          ) {
            if (!matches.find((m) => m.id === newGrid[row][i].id)) {
              newGrid[row][i].isMatched = true;
              newGrid[row][i].comboGroup = comboGroup;
              matches.push(newGrid[row][i]);
            }
          }
        }
      }
    }

    // Check vertical matches
    for (let col = 0; col < GRID_SIZE; col++) {
      for (let row = 0; row < GRID_SIZE - 2; row++) {
        if (
          newGrid[row][col].type === newGrid[row + 1][col].type &&
          newGrid[row][col].type === newGrid[row + 2][col].type
        ) {
          if (!newGrid[row][col].comboGroup) comboGroup++;
          for (
            let i = row;
            i < GRID_SIZE && newGrid[i][col].type === newGrid[row][col].type;
            i++
          ) {
            if (!matches.find((m) => m.id === newGrid[i][col].id)) {
              newGrid[i][col].isMatched = true;
              newGrid[i][col].comboGroup =
                newGrid[i][col].comboGroup || comboGroup;
              matches.push(newGrid[i][col]);
            }
          }
        }
      }
    }

    if (matches.length > 0) {
      setGrid(newGrid);
      processMatches(matches);
      saveReplayFrame();
    }
  };

  const processMatches = (matches: PuzzlePiece[]) => {
    const damage = matches.length * 5;
    const mpGain = matches.length * 2;

    setGameState((prev) => ({
      ...prev,
      enemyHP: Math.max(0, prev.enemyHP - damage),
      playerMP: Math.min(100, prev.playerMP + mpGain),
      combo: prev.combo + 1,
      score: prev.score + matches.length * 100 * (prev.combo + 1),
    }));

    // Add timeline event
    const event: TimelineEvent = {
      id: Date.now().toString(),
      type: "attack",
      target: "enemy",
      damage,
      timestamp: Date.now(),
      position: 0,
    };
    setTimelineEvents((prev) => [...prev, event]);

    // Clear matches after animation
    setTimeout(() => {
      dropPieces();
      saveReplayFrame();
    }, 500);
    triggerEnemyTurn();
  };

  const dropPieces = () => {
    const newGrid = [...grid];

    // Drop existing pieces
    for (let col = 0; col < GRID_SIZE; col++) {
      let emptyRow = GRID_SIZE - 1;
      for (let row = GRID_SIZE - 1; row >= 0; row--) {
        if (!newGrid[row][col].isMatched) {
          if (row !== emptyRow) {
            newGrid[emptyRow][col] = newGrid[row][col];
            newGrid[emptyRow][col].row = emptyRow;
          }
          emptyRow--;
        }
      }

      // Fill empty spaces with new pieces
      for (let row = emptyRow; row >= 0; row--) {
        newGrid[row][col] = {
          id: `${row}-${col}-${Date.now()}`,
          type: PIECE_TYPES[Math.floor(Math.random() * PIECE_TYPES.length)],
          row,
          col,
        };
      }
    }

    setGrid(newGrid);

    // Check for new matches
    setTimeout(() => {
      checkMatches();
    }, 300);
  };

  const handlePieceHover = (piece: PuzzlePiece | null) => {
    if (piece && selectedPiece && isAdjacent(selectedPiece, piece)) {
      const result = simulateSwap(selectedPiece, piece);
      setPredictedMoves(result.matches);
      setPredictionData(result);
    } else {
      setPredictedMoves([]);
      setPredictionData(null);
    }
  };

  const simulateSwap = (
    piece1: PuzzlePiece,
    piece2: PuzzlePiece
  ): PredictionResult => {
    const tempGrid = grid.map((row) => [...row]);
    const temp = tempGrid[piece1.row][piece1.col];
    tempGrid[piece1.row][piece1.col] = tempGrid[piece2.row][piece2.col];
    tempGrid[piece2.row][piece2.col] = temp;

    const matchesMap: Record<string, PuzzlePiece> = {};
    let comboCount = 0;

    // Simulate matches (horizontal + vertical)
    for (let row = 0; row < GRID_SIZE; row++) {
      for (let col = 0; col < GRID_SIZE - 2; col++) {
        if (
          tempGrid[row][col].type === tempGrid[row][col + 1].type &&
          tempGrid[row][col].type === tempGrid[row][col + 2].type
        ) {
          comboCount++;
          for (
            let i = col;
            i < GRID_SIZE && tempGrid[row][i].type === tempGrid[row][col].type;
            i++
          ) {
            matchesMap[tempGrid[row][i].id] = tempGrid[row][i];
          }
        }
      }
    }

    for (let col = 0; col < GRID_SIZE; col++) {
      for (let row = 0; row < GRID_SIZE - 2; row++) {
        if (
          tempGrid[row][col].type === tempGrid[row + 1][col].type &&
          tempGrid[row][col].type === tempGrid[row + 2][col].type
        ) {
          comboCount++;
          for (
            let i = row;
            i < GRID_SIZE && tempGrid[i][col].type === tempGrid[row][col].type;
            i++
          ) {
            matchesMap[tempGrid[i][col].id] = tempGrid[i][col];
          }
        }
      }
    }

    const matches = Object.values(matchesMap);
    return {
      matches,
      damage: matches.length * 5,
      mpGain: matches.length * 2,
      comboCount,
    };
  };

  const useAbility = (ability: Ability) => {
    if (
      replayMode ||
      ability.currentCooldown > 0 ||
      displayGameState.playerMP < ability.cost
    )
      return;

    setGameState((prev) => ({
      ...prev,
      playerMP: prev.playerMP - ability.cost,
      enemyHP:
        ability.damage > 0
          ? Math.max(0, prev.enemyHP - ability.damage)
          : prev.enemyHP,
      playerHP:
        ability.damage < 0
          ? Math.min(100, prev.playerHP - ability.damage)
          : prev.playerHP,
    }));

    setAbilities((prev) =>
      prev.map((a) =>
        a.id === ability.id ? { ...a, currentCooldown: a.cooldown } : a
      )
    );

    const event: TimelineEvent = {
      id: Date.now().toString(),
      type: "special",
      target: ability.damage > 0 ? "enemy" : "player",
      damage: Math.abs(ability.damage),
      timestamp: Date.now(),
      position: 0,
    };
    setTimelineEvents((prev) => [...prev, event]);
    triggerEnemyTurn();
  };

  const handleSplitterDrag = useCallback(
    (e: MouseEvent) => {
      if (!isDraggingSplitter || !containerRef.current) return;

      const rect = containerRef.current.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const percentage = (x / rect.width) * 100;
      setSplitterPosition(Math.min(Math.max(percentage, 30), 85));
    },
    [isDraggingSplitter]
  );

  useEffect(() => {
    if (isDraggingSplitter) {
      document.addEventListener("mousemove", handleSplitterDrag);
      document.addEventListener("mouseup", () => setIsDraggingSplitter(false));
      return () => {
        document.removeEventListener("mousemove", handleSplitterDrag);
        document.removeEventListener("mouseup", () =>
          setIsDraggingSplitter(false)
        );
      };
    }
  }, [isDraggingSplitter, handleSplitterDrag]);

  useEffect(() => {
    if (!replayMode || replayHistory.length === 0) return;
    const interval = setInterval(() => {
      if (currentReplayIndex < replayHistory.length) {
        const frame = replayHistory[currentReplayIndex];
        setGrid(frame.grid);
        setGameState(frame.gameState);
        setCurrentReplayIndex((i) => i + 1);
      } else {
        setReplayMode(false);
        setCurrentReplayIndex(0);
        clearInterval(interval);
      }
    }, 800);
    return () => clearInterval(interval);
  }, [replayMode, currentReplayIndex, replayHistory]);

  const triggerEnemyTurn = () => {
    if (displayGameState.enemyHP <= 0 || displayGameState.playerHP <= 0) return;
    const damage = Math.floor(Math.random() * 10) + 5;
    setGameState((prev) => {
      const updated = {
        ...prev,
        playerHP: Math.max(0, prev.playerHP - damage),
        turn: prev.turn + 1,
      };
      saveReplayFrame();
      return updated;
    });
    const event: TimelineEvent = {
      id: Date.now().toString(),
      type: "attack",
      target: "player",
      damage,
      timestamp: Date.now(),
      position: 0,
    };
    setTimelineEvents((prev) => [...prev, event]);
    saveReplayFrame();
  };

  const handleReplayToggle = () => {
    if (!replayMode && replayHistory.length > 0) {
      setCurrentReplayIndex(0);
      setReplayMode(true);
    } else {
      setReplayMode(false);
      setReplayHistory([]);
    }
  };

  //  Reset game state
  const resetGame = () => {
    initializeGrid();
    setGameState({
      playerHP: 100,
      enemyHP: 100,
      playerMP: 50,
      turn: 1,
      combo: 0,
      score: 0,
    });
    setTimelineEvents([]);
  };

  // Update ability cooldowns
  useEffect(() => {
    const cooldownTimer = setInterval(() => {
      setAbilities((prev) =>
        prev.map((a) => ({
          ...a,
          currentCooldown: Math.max(0, a.currentCooldown - 1),
        }))
      );
    }, 1000);

    return () => clearInterval(cooldownTimer);
  }, []);

  const playerPathId = `timeline-path-player`;
  const enemyPathId = `timeline-path-enemy`;
  const playerEvents = timelineEvents.filter((e) => e.target === "player");
  const enemyEvents = timelineEvents.filter((e) => e.target === "enemy");

  const Bars3 = (props: React.SVGProps<SVGSVGElement>) => {
    return (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 20 20"
        width="2rem"
        height="2rem"
        {...props}
      >
        <path
          fill="currentColor"
          fillRule="evenodd"
          d="M2 4.75A.75.75 0 0 1 2.75 4h14.5a.75.75 0 0 1 0 1.5H2.75A.75.75 0 0 1 2 4.75ZM2 10a.75.75 0 0 1 .75-.75h14.5a.75.75 0 0 1 0 1.5H2.75A.75.75 0 0 1 2 10Zm0 5.25a.75.75 0 0 1 .75-.75h14.5a.75.75 0 0 1 0 1.5H2.75a.75.75 0 0 1-.75-.75Z"
          clipRule="evenodd"
        ></path>
      </svg>
    );
  };

  return (
    <>
      <style>
        {`
          .abilities {
            padding: 1.5rem;
            height: auto;
            width: 100%;
          }

          .puzzle {
            flex: 1;
            padding: 1.5rem;
            height: auto;
            width: 100%;
          }

          @media (min-width: 1024px) {
            .abilities {
              height: 100%;
              width: ${abilitiesWidthLg}%;
            }

            .puzzle {
              order: 0;
              height: 100%;
              width: ${puzzleWidthLg}%;
            }
          }
        `}
      </style>
      <div className="relative lg:min-h-screen w-screen bg-gray-700 flex items-center justify-center p-4 overflow-auto lg:overflow-hidden">
        <div
          ref={containerRef}
          className="lg:w-[1600px] lg:h-[calc(100vh-30px)] bg-gray-900 rounded-2xl shadow-2xl backdrop-blur-md flex flex-col lg:overflow-hidden"
        >
          {/* Header */}
          <div className="bg-gradient-to-r from-[#260801] to-[#401A04] p-4 flex justify-between items-center rounded-tr-2xl rounded-tl-2xl ">
            {/* Left Side: Logo + Info */}
            <div className="flex flex-col sm:flex-row sm:items-center gap-4 sm:gap-4">
              <h1 className="text-2xl md:text-3xl font-bold bg-gradient-to-r from-[#F2EB80] via-[#BF6A1F] to-red-500 bg-clip-text text-transparent">
                Puzzle Battle
              </h1>
              <div className="flex gap-2 md:gap-4">
                <span className="px-2 md:px-3 py-1 bg-[#BF6A1F] rounded-full text-white text-xs md:text-sm">
                  Turn {displayGameState?.turn ?? "--"}
                </span>
                <span className="px-2 md:px-3 py-1 bg-[#8C441B] rounded-full text-white text-xs md:text-sm">
                  Score: {displayGameState?.score ?? 0}
                </span>
                <span className="px-2 md:px-3 py-1 bg-[#F2EB80] rounded-full text-[#401A04] text-xs md:text-sm font-semibold">
                  Combo x{displayGameState.combo}
                </span>
              </div>
            </div>

            {/* Mobile Menu Toggle */}
            <div className="sm:hidden">
              <button
                onClick={() => setMenuOpen(!menuOpen)}
                className="text-[#F2EB80] p-2"
              >
                <Bars3 />
              </button>
              {menuOpen && (
                <div className="absolute top-16 right-4 bg-[#401A04] rounded-lg shadow-lg flex flex-col gap-2 p-4 z-50">
                  <button
                    onClick={() => {
                      handleReplayToggle();
                      setMenuOpen(false);
                    }}
                    className="px-4 py-2 bg-[#8C441B] hover:bg-[#BF6A1F] text-white rounded-lg transition"
                  >
                    {replayMode ? "Exit Replay" : "View Replay"}
                  </button>
                  <button
                    onClick={() => {
                      resetGame();
                      setMenuOpen(false);
                    }}
                    className="px-4 py-2 bg-[#8C441B] hover:bg-[#BF6A1F] text-white rounded-lg transition"
                  >
                    New Game
                  </button>
                </div>
              )}
            </div>

            {/* Desktop Buttons */}
            <div className="hidden sm:flex gap-4">
              <button
                onClick={handleReplayToggle}
                className="px-4 py-2 bg-[#8C441B] hover:bg-[#BF6A1F] text-white rounded-lg transition"
              >
                {replayMode ? "Exit Replay" : "View Replay"}
              </button>
              <button
                onClick={resetGame}
                className="px-4 py-2 bg-[#8C441B] hover:bg-[#BF6A1F] text-white rounded-lg transition"
              >
                New Game
              </button>
            </div>
          </div>

          {/* HP/MP Bars */}
          <div className="p-6 flex flex-col md:flex-row justify-center gap-8">
            <div className="flex-1 bg-gray-800 rounded-lg p-4">
              <div className="flex justify-between items-center mb-2">
                <span className="text-[#F2EB80] font-semibold">Player</span>
                <span className="text-green-400">
                  {displayGameState.playerHP}/100 HP
                </span>
              </div>
              <div className="h-4 bg-black/40 rounded-full overflow-hidden mb-4">
                <div
                  className="h-full bg-gradient-to-r from-green-500 to-green-600 transition-all duration-300"
                  style={{ width: `${displayGameState.playerHP}%` }}
                />
              </div>
              <div className="h-2 bt-2 bg-black/40 rounded-full overflow-hidden">
                <div
                  className="h-full bg-gradient-to-r from-blue-500 to-blue-600 transition-all duration-300"
                  style={{ width: `${displayGameState.playerMP}%` }}
                />
              </div>
            </div>
            <div className="flex-1 bg-gray-800 rounded-lg p-4">
              <div className="flex justify-between items-center mb-2">
                <span className="text-[#F2EB80] font-semibold">Enemy</span>
                <span className="text-red-400">
                  {displayGameState.enemyHP}/100 HP
                </span>
              </div>
              <div className="h-4 bg-black/40 rounded-full overflow-hidden">
                <div
                  className="h-full bg-gradient-to-r from-red-500 to-red-600 transition-all duration-300"
                  style={{ width: `${displayGameState.enemyHP}%` }}
                />
              </div>
            </div>
          </div>

          {/* Timelines */}
          <div className="px-4 flex gap-4 justify-center space-y-2">
            {/* Player timeline */}
            <div
              className="relative h-20 w-full bg-gray-800 rounded-lg p-2 m-2 backdrop-blur-sm flex flex-col justify-between gap-2 overflow-visible
"
            >
              <div className="text-sm font-semibold text-green-400">
                Player Timeline
              </div>
              <svg
                className="absolute inset-0 top-6 w-full h-full"
                viewBox="0 0 600 120"
                preserveAspectRatio="none"
              >
                <defs>
                  <path
                    id={playerPathId}
                    d="M0,40 Q150,0 300,40 T 600,40"
                    fill="none"
                    stroke="#4ADE80"
                  />
                </defs>

                {/* Render the timeline path */}
                <use
                  href={`#${playerPathId}`}
                  stroke="#F2EB80"
                  strokeWidth="2"
                  opacity="0.3"
                />

                {/* Animate using refs to force mount timing */}
                {playerEvents.slice(-10).map((event, index) => (
                  <g key={event.id}>
                    <animateMotion
                      dur="10s"
                      repeatCount="1"
                      rotate="auto"
                      begin={`${index * 0.5}s`}
                    >
                      <mpath href={`#${playerPathId}`} />
                    </animateMotion>
                    <circle
                      key={event.id}
                      r="12"
                      fill={
                        event.type === "attack"
                          ? "#BF6A1F"
                          : event.type === "defend"
                          ? "#8C441B"
                          : "#F2EB80"
                      }
                      className="animate-pulse"
                    ></circle>
                    <text
                      y="4"
                      textAnchor="middle"
                      fontSize="12"
                      fill="white"
                      className="font-bold"
                    >
                      {event.damage || ""}
                    </text>
                  </g>
                ))}
              </svg>
            </div>

            {/* Enemy timeline */}
            <div
              className="relative h-20 w-full bg-gray-800 rounded-lg p-2 m-2 backdrop-blur-sm flex flex-col justify-between gap-2 overflow-visible
"
            >
              <div className="text-sm font-semibold text-red-400">
                Enemy Timeline
              </div>
              <svg
                className="absolute inset-0 top-6 w-full h-full"
                viewBox="0 0 600 120"
                preserveAspectRatio="none"
              >
                <defs>
                  <path
                    id={enemyPathId}
                    d="M0,40 Q150,0 300,40 T600,40"
                    fill="none"
                    stroke="#F87171"
                  />
                </defs>

                {/* Render the timeline path */}
                <use
                  href={`#${enemyPathId}`}
                  stroke="#F2EB80"
                  strokeWidth="2"
                  opacity="0.3"
                />

                {/* Animate using refs to force mount timing */}
                {enemyEvents.slice(-10).map((event, index) => (
                  <g key={event.id}>
                    <animateMotion
                      dur="10s"
                      repeatCount="1"
                      rotate="auto"
                      begin={`${index * 0.5}s`}
                    >
                      <mpath href={`#${enemyPathId}`} />
                    </animateMotion>
                    <circle
                      r="12"
                      fill={
                        event.type === "attack"
                          ? "#BF6A1F"
                          : event.type === "defend"
                          ? "#8C441B"
                          : "#F2EB80"
                      }
                      className="animate-pulse"
                    />
                    <text
                      y="4"
                      textAnchor="middle"
                      fontSize="12"
                      fill="white"
                      className="font-bold"
                    >
                      {event.damage || ""}
                    </text>
                  </g>
                ))}
              </svg>
            </div>
          </div>

          {/* Main Game Area */}
          <div className="h-full flex-1 flex flex-col lg:flex-row relative ">
            {/* Puzzle Grid */}
            <div className="puzzle">
              <div className="h-full flex items-center justify-center p-6 bg-gray-800 rounded-xl shadow-inner">
                <div className="grid justify-items-center grid-cols-8 gap-2 lg:p-4 rounded-xl backdrop-blur-sm">
                  {predictionData && predictedMoves.length > 0 && (
                    <div className="absolute top-2 left-1/2 transform -translate-x-1/2 bg-white/80 text-black px-3 py-1 rounded-md shadow z-50 text-sm font-medium">
                      üî• {predictionData.damage} DMG, üíß +
                      {predictionData.mpGain} MP, x{predictionData.comboCount}{" "}
                      Combo
                    </div>
                  )}
                  {grid.map((row, rowIndex) =>
                    row.map((piece, colIndex) => (
                      <div
                        key={piece.id}
                        className={`
    relative w-8 h-8 lg:w-14 lg:h-14 rounded-lg cursor-pointer transition-all duration-300 transform
    ${
      selectedPiece?.id === piece.id
        ? "ring-4 ring-[#F2EB80] scale-110 z-10"
        : ""
    }
    ${piece.isMatched ? "scale-0 opacity-0" : "scale-100 opacity-100"}
    hover:scale-105 active:scale-95
  `}
                        style={{
                          backgroundColor: PIECE_COLORS[piece.type],
                          boxShadow: "0 0 4px rgba(0,0,0,0.3)",
                        }}
                        onClick={() => handlePieceClick(piece)}
                        onMouseEnter={() => handlePieceHover(piece)}
                        onMouseLeave={() => handlePieceHover(null)}
                      >
                        <div className="w-full h-full flex items-center justify-center text-2xl select-none">
                          {piece.type === "fire" && "üî•"}
                          {piece.type === "water" && "üíß"}
                          {piece.type === "earth" && "üåç"}
                          {piece.type === "air" && "üí®"}
                          {piece.type === "light" && "‚ú®"}
                          {piece.type === "dark" && "üåô"}
                        </div>

                        {/* Combo Label */}
                        {piece.comboGroup && (
                          <div className="absolute -top-2 -right-2 w-6 h-6 bg-[#F2EB80] rounded-full flex items-center justify-center text-xs font-bold text-[#401A04]">
                            {piece.comboGroup}
                          </div>
                        )}

                        {/* Predicted Match Highlight */}
                        {predictedMoves.find((p) => p.id === piece.id) && (
                          <div className="absolute inset-0 bg-white/70 rounded-lg z-10 flex items-center justify-center text-xs font-bold text-neutral-900 pointer-events-none">
                            Match?
                          </div>
                        )}
                      </div>
                    ))
                  )}
                </div>
              </div>
            </div>

            {/* Splitter */}
            <div
              className="hidden lg:block w-1 h-full hover:bg-[#8C441B] cursor-ew-resize transition-colors relative"
              onMouseDown={() => setIsDraggingSplitter(true)}
            >
              <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-8 h-16 flex items-center justify-center">
                <div className="w-1 h-full bg-[#F2EB80] opacity-50" />
              </div>
            </div>

            {/* Abilities Bar */}
            <div className="abilities">
              <div className=" p-6 h-full w-full bg-gray-800 lg:overflow-y-auto rounded-xl shadow-inner">
                <h3 className="text-xl font-bold text-[#F2EB80] mb-6">
                  Abilities
                </h3>
                <div className="space-y-6">
                  {abilities.map((ability) => (
                    <div
                      key={ability.id}
                      className={`
                    p-4 lg:p-6 bg-gradient-to-r from-[#401A04] to-[#8C441B] hover:from-[#8C441B] hover:to-[#BF6A1F] rounded-lg cursor-pointer
                    transition-all duration-300 transform hover:scale-105
                    ${
                      ability.currentCooldown > 0 ||
                      displayGameState.playerMP < ability.cost
                        ? "opacity-50 cursor-not-allowed"
                        : ""
                    }
                  `}
                      onClick={() => {
                        useAbility(ability);
                      }}
                    >
                      <div className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                          <span className="text-3xl">{ability.icon}</span>
                          <div>
                            <h4 className="text-[#F2EB80] text-lg font-semibold mb-2">
                              {ability.name}
                            </h4>
                            <p className="text-xs font-semibold text-white">
                              {ability.damage > 0
                                ? `${ability.damage} DMG`
                                : `${-ability.damage} HEAL`}
                            </p>
                          </div>
                        </div>
                        <div className="text-right">
                          <div className="text-[#F2EB80] text-sm font-semibold">
                            {ability.cost} MP
                          </div>
                          {ability.currentCooldown > 0 && (
                            <div className="text-white/70 text-xs">
                              CD: {ability.currentCooldown}s
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* Game Over Overlay */}
          {(displayGameState.playerHP <= 0 ||
            displayGameState.enemyHP <= 0) && (
            <div className="absolute inset-0 bg-black/80 flex items-center justify-center backdrop-blur-sm">
              <div className="bg-gradient-to-br from-[#401A04] to-[#8C441B] p-8 rounded-2xl text-center">
                <h2 className="text-4xl font-bold text-[#F2EB80] mb-4">
                  {displayGameState.playerHP <= 0 ? "Game Over" : "Victory!"}
                </h2>
                <p className="text-2xl text-white mb-6">
                  Final Score: {displayGameState.score}
                </p>
                <button
                  onClick={resetGame}
                  className="px-8 py-3 bg-[#F2EB80] hover:bg-[#BF6A1F] text-[#401A04] hover:text-white rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
                >
                  Play Again
                </button>
              </div>
            </div>
          )}
        </div>
        {replayMode && (
          <div className="absolute inset-0 bg-black/50 flex items-center justify-center z-50 pointer-events-none">
            <p className="text-[#F2EB80] text-xl font-semibold">Replaying...</p>
          </div>
        )}
      </div>
    </>
  );
};

export default PuzzleBattleGame;
